.navbar{
    
    height: 50px;
    border-bottom: 0.5px solid rgb(233, 228, 228);
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;
    

    .wrapper{
        width: 100%;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .search{
            display: flex;
            border: 1px solid lightgray;
            padding: 3px;
            border-radius: 5px;

            input{
                border: none;
                outline: none;
                background-color: transparent;
                &::placeholder{
                    font-size: 12px;
                    color: lightgray;
                }
            }

        }
        
        .items{
            display: flex;
            align-items: center;
            
            .item{
                display: flex;
                align-items: center;
                margin: 20px;
                cursor: pointer;
                position: relative;

                .icon{
                    font-size: 20px;
                }
                .avatar{
                    height: 30px;
                    width: 30px;
                    border-radius: 50%;
                    border: 1px solid lightblue;
                }
                .counter{
                    height: 15px;
                    width: 15px;
                    border-radius: 50%;
                    background-color: red;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: white;
                    font-size: 10px;
                    font-weight: bold;
                    position: absolute;
                    top: -5px;
                    right: -5px;
                }
            }
        }
    }

}